.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: -1;
    background: rgb(2, 0, 36);
    background: linear-gradient(150deg, rgba(2, 0, 36, 1) 0%, rgba(56, 49, 157, 1) 0%, rgba(255, 156, 156, 1) 0%, rgba(0, 212, 255, 1) 100%, rgba(119, 104, 117, 1) 100%);
}